.works{
	padding: 0 3rem 0 3rem;
	display: flex;
	height: 90vh;
	margin-top: 4rem;

}

.awesome{
	display: flex;
	flex-direction: column;
	position: relative;
}
.awesome>:nth-child(1){
	color: var(--black);
	font-size: 2.5rem;
	font-weight: bold;
}

.awesome>:nth-child(2){
	color: var(--orange);
	font-size: 2.5rem;
	font-weight: bold;
}
.awesome>:nth-child(3){
	color: var(--gray);
	font-size: 14px;
	margin-top: 1rem;
}


.w-left{
	width: 50%;
}



/*Right side*/

.w-right{
	position: relative;
	width: 50%;
}
.w-mainCircle{
	left: 9rem;
	position: relative;
	width: 18rem;
	height: 18rem;
	border-radius: 100%;
	box-shadow: var(--smboxShadow);
	top: 2rem;
	background: white;
}
.w-mainCircle>*{
	position: absolute;
}

.w-mainCircle>:nth-child(1){
	top: -3rem;
	left: 6rem;
}

.w-mainCircle>:nth-child(2){
	top: 5rem;
	left: -3rem;
}

.w-mainCircle>:nth-child(3){
	top: 5rem;
	left: 6rem;
}

.w-mainCircle>:nth-child(4){
	top: 5rem;
	left: 15rem;
}
.w-mainCircle>:nth-child(5){
	top: 13rem;
	left: 6rem;
}
.w-secCircle{
	width: 6rem;
	height: 6rem;
	position: absolute;
	border-radius: 100%;
	border: 5px solid #ebebeb;
	display: flex;
	align-items: center;
	justify-content: center;
	background: none;
	box-shadow: var(--smboxShadow);
}
.w-secCircle > img{
	transform: scale(0.6);
}
.w-backCircle{
	position: absolute;
	height: 13rem;
	width: 13rem;
	border-radius: 100%;
	z-index: -1;
}
.blueCircle{
	background: #1949B8;
	left: 18rem;
	top: 0rem;
}
.yellowCircle{
	background: #F5C32C;
	left: 18rem;
	top: 8rem;
}

@media screen and (max-width: 480px) {
  .works {
    flex-direction: column;
    height: 55rem;
    padding: 0;
    gap: 5rem;
  }

  .w-right {
    display: flex;
    align-items: flex-start;
    justify-content:start;
    transform: scale(0.7);
  }
  .w-mainCircle{
    position: static;
  }
  .w-backCircle{
    left: 8rem!important;
  }

}
