.intro{
	display: flex;
	height: 77vh;
	margin-top: 6rem;

}
.i-left{
	display: flex;
	position: relative;
	flex: 1;
	flex-direction: column;
	gap: 2rem;
}
.i-name{
	display: flex;
	flex-direction: column;
}
.i-name>:nth-child(1){
	color: var(--black);
	font-weight: bold;
	font-size: 3rem;
}

.i-name>:nth-child(2){
	color: var(--orange);
	font-weight: bold;
	font-size: 3rem;
}
.i-name>:nth-child(3){
	font-weight: 100;
	font-size: 14px;
	color: var(--gray);
	margin-top: 10px;
}
.i-button{
	width: 6rem;
	height: 2rem;
}

.i-icons{
	margin-top: 3rem;
	display: flex;
	gap: 0rem;
}
/*//target all images*/

.i-icons>*{
	transform: scale(0.5);
}

.i-icons>*:hover{
	cursor: pointer;
}


.i-right{
	flex: 1;
	position: relative;
}
.i-right>*{
	position: absolute;
	z-index: 1;
}
.i-right>:nth-child(1){
	transform: scale(0.69);
	left: -15%;
	top: -9%;
}
.i-right>:nth-child(2){
	top: -4.6rem;
	transform: scale(0.6);
	left: -3rem;
}

.i-right>:nth-child(3){
	transform: scale(1);
	left: 24%;
	margin-top: -3rem;
}

.i-right>:nth-child(4){
	transform: scale(0.6);
	top: -19%;
	left: -24%;
	border-radius: 50%;
	padding: 0;
}
.blur{
	position: absolute;
	width: 22rem;
	height: 14rem;
	border-radius: 50%;
	background: #edd0ff;
	z-index: -9;
	top: -18%;
	left: 56%;
	filter: blur(72px);
}
@media screen and (max-width: 480px) {
  .Intro {
    flex-direction: column;
    gap: 7rem;
    height: 64rem;
  }

  .i-right {
    transform: scale(0.8);
    left: -3rem;
  }
  .i-right .blur {
    display: none;
  }
  .floating-div:nth-of-type(1){
    /* display: none; */
    top: -7rem !important;;
  }
}
